@import url(https://fonts.googleapis.com/css2?family=Abel&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abel&display=swap);
* {
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.navbar {
  background-color: #001457 !important;
}
.link {
  font-size: large;
  font-weight: 600;
  display: inline;
  float: right;
}

@media (min-width: 992px) {
  .link {
    margin-left: 2em;
    margin-right: 6em;
  }
  .ml-1 {
    margin-left: 19em;
  }
  .app-name {
    margin-left: 3em;
  }
  .about-us,
  .contact {
    margin-left: 1em;
  }
}

.col1 {
  margin-top: 5rem;
}

.title {
  text-align: center;
  margin-top: -3rem;
  font-family: "Abel", sans-serif;
  background-color: #001457;
  color: white;
  border-radius: 10px;
  font-size: xx-large;
  width: 12rem;
}
.btn {
  margin-top: 5px;
  background-color: #001457;
}
.box {
  margin-top: 5rem;
  margin-bottom: 2rem;
  padding: 1rem;
  box-shadow: 0 4px 8px 0 rgb(0 0 0 / 30%), 0 6px 20px 0 rgb(0 0 0 / 30%);
  border-radius: 10px;
}
.rowx {
  -webkit-justify-content: center;
          justify-content: center;
}

h4,
h5,
h6 {
  font-family: "Abel", sans-serif;
}
.img {
  border-radius: 49%;
  box-shadow: 0 4px 8px 0 rgb(0 0 0 / 30%), 0 6px 20px 0 rgb(0 0 0 / 30%);
}
.imgOne {
  margin-top: 10px;
}
.imgTwo {
  margin-top: 10px;
}
.rowOne {
  background-color: #b2ecfd;
  padding-top: 4px;
  padding-bottom: 4px;
  box-shadow: 0 4px 8px 0 rgb(0 0 0 / 30%), 0 6px 20px 0 rgb(0 0 0 / 30%);
}
.rowTwo {
  background-color: #095a9d;
  color: white;
  padding-top: 4px;
  padding-bottom: 4px;
  box-shadow: 0 4px 8px 0 rgb(0 0 0 / 30%), 0 6px 20px 0 rgb(0 0 0 / 30%);
  text-align: right;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.count-left {
  display: inline;
  float: left;
  text-align: center;
}

.count-right {
  display: inline;
  float: right;
  text-align: center;
}

.head-bg {
  background-color: #b2ecfd;
  opacity: 0.7;
  padding-bottom: 50px;
  margin-bottom: 50px;
}

h1 {
  color: #000;
}

h3 {
  color: #095a9d;
}

.btn {
  background-color: #095a9d;
  color: #fff;
  border: none;
}

.btn:hover {
  background-color: #b2ecfd;
  color: #000;
  border: none;
}



